.container {
  width: 100%;
  height: 100vh;
  background-color: white;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center; }

.lottie_container {
  width: 50%; }

.form_container {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start; }

.input_wrapper {
  width: 400px; }

.input {
  margin-bottom: 15px; }

.sign_up {
  cursor: pointer;
  margin-bottom: 10px;
  &:hover {
    text-decoration: underline; } }
